import {__} from '@wordpress/i18n';
import {useBlockProps, RichText, MediaUpload, MediaUploadCheck} from '@wordpress/block-editor';
import {Button} from '@wordpress/components';
import {Fragment} from '@wordpress/element';

export default function Edit({attributes, setAttributes}) {
	const {imageUrl, imageAlt, heading, text} = attributes;

	const onSelectImage = (media) => {
		setAttributes({
			imageUrl: media.url,
			imageAlt: media.alt,
		});
	};

	const blockProps = useBlockProps({
		className: 'inno-icon-content',
	});

	return (
		<div {...blockProps}>
			<MediaUploadCheck>
				<MediaUpload
					onSelect={onSelectImage}
					allowedTypes={['image']}
					render={({open}) => (
						<Fragment>
							{imageUrl ? (
								<div className="inno-icon-content__image-wrapper">
									<img
										src={imageUrl}
										alt={imageAlt || ''}
										className="inno-icon-content__image"
									/>
									<Button
										onClick={() =>
											setAttributes({imageUrl: '', imageAlt: ''})
										}
										isDestructive
										variant="secondary"
										className="inno-icon-content__remove"
									>
										{__('X', 'tsc')}
									</Button>
								</div>
							) : (
								<Button onClick={open} variant="primary">
									{__('Bild ausw√§hlen', 'tsc')}
								</Button>
							)}
						</Fragment>
					)}
				/>
			</MediaUploadCheck>
			<div className="inno-icon-content__body">
				<RichText
					tagName="p"
					value={heading}
					onChange={(value) => setAttributes({heading: value})}
					placeholder={__('Fettschrift eingeben...', 'tsc')}
					className="inno-icon-content__strong"
				/>

				<RichText
					tagName="p"
					value={text}
					onChange={(value) => setAttributes({text: value})}
					placeholder={__('Text eingeben...', 'tsc')}
					className="inno-icon-content__text"
				/>
			</div>
		</div>
	);
}
