import {useBlockProps, RichText} from '@wordpress/block-editor';

export default function save({attributes}) {
	const {imageUrl, imageAlt, heading, text} = attributes;

	return (
		<div {...useBlockProps.save({className: 'inno-icon-content'})}>
			{imageUrl && (
				<div className="inno-icon-content__image-wrapper">
					<img
						src={imageUrl}
						alt={imageAlt || ''}
						className="inno-icon-content__image"
					/>
				</div>
			)}
			<div className="inno-icon-content__body">
				{heading && (
					<RichText.Content
						tagName="p"
						value={heading}
						className="inno-icon-content__strong"
					/>
				)}

				{text && (
					<RichText.Content
						tagName="p"
						value={text}
						className="inno-icon-content__text"
					/>
				)}
			</div>
		</div>
	);
}
